body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body{
  font-family: 'Montserrat', sans-serif;
}
.fullpage{
height: 100vh;
display: flex;
flex: auto;
flex-direction: column;
justify-content: center;
align-items: center;
background-color: #13657B;
color: #A2F5E6
}

.icon {
    position: relative;
    top: 20vh;
    cursor: pointer;
}
.nav .first-button {
    position: fixed;
    top: 20px;
    right: 50px;
    background-color: #13657B;
    border: #A2F5E6;
    outline-color: #13657B;
    font-size: 40px;
    color: #A2F5E6;
    cursor: pointer
}
.nav .second-button{
    position: fixed;
    top: 20px;
    right: 190px;
    background-color: #13657B;
    border: #A2F5E6;
    outline-color: #13657B;
    font-size: 40px;
    color: #A2F5E6;
    cursor: pointer
}
.paragraphs{
    margin: 60px;
    margin-top: 20px;
}
.app-card-list {
    display: flex;
    flex: auto;
    flex-direction: row;
    justify-content: center;
}
.card {
    margin: 50px 30px ;
    height: 200px;
    width: 120px;
    background-color: #282c34;
}    
h1 {
    font-size: 50px;
    margin-top: 130px
}
p {
    opacity: .5
}


.top-icon {
    position: relative;
    top: 57vh;
    right:280px;
    cursor: pointer;
}
.theme button{
    position: fixed;
    top: 20px;
    left: 30px;
    background-color: #13657B;
    border: #A2F5E6;
    outline-color: #13657B;
    font-Size: 20px;
    color: #A2F5E6; 
    cursor:pointer
}
